import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LocalService } from '../../services/local.service';
import { RestService } from '../../services/rest.service';
import { UtilityService } from '../../services/utility.service';
import { EventsService } from '../../services/events.service';

@Component({
  selector: 'app-cleanerforgot',
  templateUrl: './cleanerforgot.page.html',
  styleUrls: ['./cleanerforgot.page.scss'],
})
export class CleanerforgotPage implements OnInit {
  email: any = '';
  password: any;
  code: any;
  cleaner: any = {};
  resetScreen = false;

  constructor(
    public localService: LocalService,
    public restService: RestService,
    public utilityService: UtilityService,
    public eventsService: EventsService,
    private router: Router
  ) {}

  ngOnInit() {}

  reset() {
    if (this.email.length < 5) {
      return this.utilityService.openAlerts(
        'Sorry!',
        'Please enter a valid email address.',
        'ok'
      );
    }
    this.restService.postData('cleaner/forgot', { email: this.email }).subscribe(
      (data) => {
        console.log(data);
        this.cleaner = data;
        this.resetScreen = true;
      },
      (err) => this.utilityService.openAlerts('Error', 'User not found.', 'ok')
    );
  }

  update() {
    if (this.code === this.cleaner.code) {
      if (this.password.length < 5) {
        return this.utilityService.openAlerts(
          'Error!',
          'Minimum 5 character required.',
          'ok'
        );
      }
      this.restService
        .postData('cleaner/reset', {
          id: this.cleaner.id,
          password: this.password,
          code: this.code,
        })
        .subscribe(
          (data) => {
            console.log(data);
            this.router.navigateByUrl('/cleanerlogin');
          },
          (err) =>
            this.utilityService.openAlerts(
              'Error',
              'Something not right at verification.',
              'ok'
            )
        );
    } else {
      return this.utilityService.openAlerts(
        'Error',
        'Invalid code provided.',
        'ok'
      );
    }
  }
}
